// for all application

export const departmentImportOngoingApplicationFunction = (builder) => {
  return builder.mutation({
    query: (data) => ({
      url: `/v2/department/${data.did}/add/application`,
      method: PATCH,
      body: data.bodyParameter,
    }),
  });
};

export const departmentOngoingApplicationListFunction = (builder) => {
  return builder.query({
    query: (data) => `/v2/department/${data.did}/all/application`,
    transformResponse: (response) => {
      let dect = decryption(response?.encrypt);
      return {
        list: dect?.ongoing,
      };
    },
    serializeQueryArgs: ({ endpointName }) => {
      return endpointName;
    },
    forceRefetch({ currentArg, previousArg }) {
      return currentArg !== previousArg;
    },
  });
};

